import "./styles.css";
import { data } from "./data";
import React, { useState, useEffect } from "react";
import Card from "./Card";

const Main = () => {
  const [checkedValues, setCheckedValues] = useState([]);
  const [check, setcheck] = useState(false);

  const checkboxdata = [
    { id: 1, text: "Software Development" },
    { id: 2, text: "Operations" },
    { id: 3, text: "Data Science" },
    { id: 4, text: "Finance" },
    { id: 5, text: "Design" }
  ];

  const handleChecked = (e) => {
    setcheck(true);

    const checked = e.target.checked;

    const hero = data[e.target.dataset.id];
    let newCheckedValues = checkedValues.filter(
      (item) => item.category !== hero.category
    );

    console.log("helo", newCheckedValues);

    if (checked) {
      console.log("heloo");
      newCheckedValues.push(hero);
      setCheckedValues(newCheckedValues);
    }

    if (checked === false) {
      newCheckedValues.splice(hero, 0);
      setCheckedValues(newCheckedValues);
      console.log(checkedValues.length);
    }
  };

  const [search, setsearch] = useState("");

  const handlechange = (event) => {
    console.log(event.target.value);
    const dataa = event.target.value;
    const res = dataa.toLowerCase();
    setsearch(res);
  };

  return (
    <>
      <div className="sidebar ">
        <div className="sidebar-content">
          <h4>COURSE CATEGORIES</h4>
          {checkboxdata.map((item, id) => (
            <div class="checbox">
              <label key={id}>
                <input
                  type="checkbox"
                  data-id={id}
                  value={item.text}
                  onChange={handleChecked}
                />

                {item.text}
              </label>
            </div>
          ))}
        </div>
      </div>

      <div class="main">
        <div className="content">
          <input
            className="inputt"
            placeholder="Search Courses"
            value={search}
            name="v"
            onChange={handlechange}
          />
        </div>

        <div className="list-card">
          {check
            ? checkedValues
                .filter((p) => {
                  if (search === "") {
                    return p;
                  } else if (p.category.toLowerCase().includes(search))
                    return p;
                })
                .map((m, index) => {
                  return <Card content={m} key={index} />;
                })
            : data
                .filter((p) => {
                  if (search === "") {
                    return p;
                  } else if (p.category.toLowerCase().includes(search))
                    return p;
                })
                .map((m, index) => {
                  return <Card content={m} key={index} />;
                })}
        </div>
      </div>
    </>
  );
};
export default Main;
